.polaroid {
    margin: 0 auto;
}

.polaroid__wrapper {
    height: 100vh;
    background-color: rgb(201, 209, 218);
    position: relative;
}

.polaroid__img {
    width: 300px;
    position: relative;
}

.polaroid__inner {
    width: 300px;
    position: relative;
    border: 1px solid darkgrey;
    background-color: white;
    padding: 15px 15px 60px 15px;
    box-shadow: 5px 10px 10px darkgrey;
    transition: 300ms;
}

.polaroid__inner:after {
    content: attr(polaroid-caption);
    position: absolute;
    bottom: 0px;
    left: 0px;
    width: calc(100% - 20px);
    padding: 10px;
    font-size: 30px;
    text-align: center;
    font-family: 'Indie Flower', cursive;
}


.polaroid__inner:hover {
    z-index: 1;
    transform: skew(-20deg, 20deg);
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
}

.polaroid__wrap:nth-child(1) {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-60%, -110%);
    -webkit-transform: translate(-60%, -110%);
    -moz-transform: translate(-60%, -110%);
    -ms-transform: translate(-60%, -110%);
    -o-transform: translate(-60%, -110%);
}

.polaroid__wrap:hover {
    z-index: 1;
}

.polaroid__wrap:nth-child(2) {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-5%, 5%);
    -webkit-transform: translate(-5%, 5%);
    -moz-transform: translate(-5%, 5%);
    -ms-transform: translate(-5%, 5%);
    -o-transform: translate(-5%, 5%);
}

.polaroid__wrap:nth-child(3) {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(15%, -90%);
    -webkit-transform: translate(15%, -90%);
    -moz-transform: translate(15%, -90%);
    -ms-transform: translate(15%, -90%);
    -o-transform: translate(15%, -90%);
}

.polaroid__wrap:nth-child(4) {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-90%, -10%);
    -webkit-transform: translate(-90%, -10%);
    -moz-transform: translate(-90%, -10%);
    -ms-transform: translate(-90%, -10%);
    -o-transform: translate(-90%, -10%);
}

.polaroid__wrap:nth-child(5) {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-110%, -80%);
    -webkit-transform: translate(-110%, -80%);
    -moz-transform: translate(-110%, -80%);
    -ms-transform: translate(-110%, -80%);
    -o-transform: translate(-110%, -80%);
}

.polaroid__wrap:nth-child(6) {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
}